import React from "react";
import { HStack, VStack, Text, Center, Box, Avatar, Link } from "native-base";

import { CDN } from "../../../constants/Config";
import { vw, vh } from "../../../util";
import CalendarWhiteIcon from "../../../components/svg/CalendarWhiteIcon";
import SqftIcon from "../../../components/svg/SqftIcon";
import AcreIcon from "../../../components/svg/AcreIcon";
import BedIcon from "../../../components/svg/BedIcon";
import BathIcon from "../../../components/svg/BathIcon";
import TimeIcon from "../../../components/svg/TimeIcon";
import moment from "moment-timezone";
import MarketIcon from "../../../components/svg/MarketIcon";

const VerticalItem = ({ item }) => {
  //default pic
  let pic = "1";
  if (item && item.pic) {
    pic = item.pic;
  }
  const createdUser = item.createdUser && item.createdUser;
  // user name
  const fullname =
    createdUser && createdUser.firstname + " " + createdUser.lastname;
  //avatar fall back
  const nameSplit = fullname.split(" ");
  const fn = nameSplit[0][0];
  const ln = nameSplit[1] ? nameSplit[1][0] : "-";
  const nameFb = fn + ln;
  // avatar image source uri
  const ui =
    createdUser && createdUser.pic && createdUser.pic.id
      ? createdUser.pic.id
      : "0";
  const userUri = `${CDN}/users/${createdUser._id}/${ui}`;
  //property image uri
  const propertyUri = `${CDN}/properties/${item._id ? item._id : "-"}/${pic}`;

  let sqft = "-";
  let acre = "-";
  if (item.sqft) {
    sqft = item.sqft !== null ? item.sqft.toString() : "-";
  }

  if (item.acre) {
    acre = item.acre !== null ? item.acre.toString() : "-";
  }
  //property owner name
  const name = item.name && item.name !== "" ? item.name.trim() : "-";

  // console.log("MID", item.mid);

  return (
    <Link
      style={{
        width: vw(343), //vw((window.screen.width - cols * 40) / cols), // vw(320), //  vw((window.screen.width - cols * 40) / cols),
        height: vh(300),
        marginHorizontal: vw(20),
        marginVertical: vh(20),
        // flex: 1,
      }}
      href={`/properties/${item._id}`}
    >
      <VStack
        style={{
          flex: 1,
          width: vw(343), //  vw((window.screen.width - cols * 40) / cols),
          height: vh(300),
          borderRadius: vw(10),
          backgroundColor: "white",
          shadowColor: "#000",
          shadowOffset: {
            width: 0,
            height: vh(3),
          },
          shadowOpacity: 0.15,
          shadowRadius: vh(10),
          elevation: vh(5),
        }}
      >
        <Box
          resizeMode='cover'
          style={{
            height: vh(150),
            borderTopStartRadius: vw(10),
            borderTopEndRadius: vw(10),
            overflow: "hidden",
            backgroundImage: `url(${propertyUri})`,
            backgroundPosition: "center",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
          }}
        >
          <VStack style={{ marginTop: vh(91), marginHorizontal: vw(10) }}>
            <Text
              style={{
                marginBottom: vh(6),
                fontFamily: "Poppins_medium",
                fontSize: vw(15),
                color: "white",
              }}
            >
              {name}
            </Text>
            <HStack justifyContent={"space-between"}>
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(10),
                  color: "white",
                  marginTop: vh(1),
                }}
              >
                {item.propertyaddress}
              </Text>
              <HStack flex={1} justifyContent={"flex-end"}>
                <Center marginTop={vh(-2)}>
                  <CalendarWhiteIcon width={vw(18)} height={vh(18)} />
                </Center>
                <Text
                  style={{
                    fontFamily: "Poppins_medium",
                    fontSize: vw(10),
                    color: "white",
                  }}
                >
                  {item.yearbuilt}
                </Text>
              </HStack>
            </HStack>
          </VStack>
          {item.mid && (
            <Center style={{ position: "absolute", right: 5, top: 5 }}>
              <MarketIcon fill={"#7d2be9"} />
            </Center>
          )}
        </Box>

        <HStack
          style={{
            marginHorizontal: vw(14),
            marginTop: vh(20),
            alignItems: "center",
            justifyContent: "space-around",
          }}
        >
          {/*Sqft*/}
          <HStack style={{ alignItems: "center" }}>
            <SqftIcon width={vw(24)} height={vh(24)} />
            <Text
              style={{
                color: "#919191",
                fontFamily: "Poppins_medium",
                fontSize: vw(13),
                lineHeight: vh(24),
                marginTop: vh(1),
              }}
            >
              {sqft}
            </Text>
          </HStack>
          {/*Acre*/}
          <HStack style={{ marginHorizontal: vw(14), alignItems: "center" }}>
            <AcreIcon width={vw(24)} height={vh(24)} />
            <Text
              style={{
                color: "#919191",
                fontFamily: "Poppins_medium",
                fontSize: vw(13),
                lineHeight: vh(24),
                marginTop: vh(1),
              }}
            >
              {acre === "-" ? acre : parseFloat(acre).toFixed(4)}
            </Text>
          </HStack>
          {/*Bed*/}
          <HStack
            style={{
              marginLeft: vw(14),
              marginRight: vw(7),
              alignItems: "center",
            }}
          >
            <BedIcon width={vw(24)} height={vh(24)} />
            <Text
              style={{
                color: "#919191",
                fontFamily: "Poppins_medium",
                fontSize: vw(13),
                lineHeight: vh(24),
                marginTop: vh(1),
              }}
            >
              {item.bed}
            </Text>
          </HStack>
          {/*Bath*/}
          <HStack style={{ marginHorizontal: vw(7), alignItems: "center" }}>
            <BathIcon width={vw(24)} height={vh(24)} />
            <Text
              style={{
                color: "#919191",
                fontFamily: "Poppins_medium",
                fontSize: vw(13),
                lineHeight: vh(24),
                marginTop: vh(1),
              }}
            >
              {item.bath}
            </Text>
          </HStack>
        </HStack>

        {/* Status and #id */}
        <HStack
          style={{
            marginTop: vh(18),
            marginHorizontal: vw(20),
            justifyContent: "space-between",
          }}
        >
          <Text
            style={{
              fontSize: vw(15),
              fontFamily: "Poppins_medium",
              color: "rgba(125, 43, 233, 1)",
            }}
          >
            {item.status && item.status.name}
          </Text>
          <Text
            style={{
              fontSize: vw(15),
              fontFamily: "Poppins_medium",
              color: "rgba(125, 43, 233, 1)",
            }}
          >
            {"#" + item.tid}
          </Text>
        </HStack>

        {/* User Block */}
        <HStack
          style={{
            marginHorizontal: vw(20),
            justifyContent: "space-between",
            marginTop: vh(20),
          }}
        >
          <HStack>
            <Center
              rounded={"full"}
              style={{
                backgroundColor: "white",
                paddingVertical: vw(2),

                marginRight: vw(10),
              }}
            >
              <Avatar
                source={{
                  uri: userUri,
                }}
                style={{
                  width: vw(24),
                  height: vh(24),
                }}
              >
                {nameFb}
              </Avatar>
            </Center>
            {/* <Text
              style={{
                paddingTop: vh(2),
                lineHeight: vh(fullname.length > 10 ? 12 : 24),
                fontSize: vw(13),
                color: "rgba(104, 104, 104, 1)",
                fontFamily: "Poppins_medium",
                width: vw(100),
                wordWrap: "break-word",
              }}
            >
              {fullname}
            </Text> */}
          </HStack>
          <HStack style={{ alignItems: "center" }}>
            <TimeIcon />
            <Text
              style={{
                // paddingTop: vh(2),
                lineHeight: vh(24),
                fontSize: vw(13),
                color: "rgba(104, 104, 104, 1)",
                fontFamily: "Poppins_medium",
              }}
            >
              {moment(item.created).format("MM/DD/YYYY - hh:mm A a")}
            </Text>
          </HStack>
        </HStack>
      </VStack>
    </Link>
  );
};

export default VerticalItem;
