import React from "react";
import { HStack, VStack, Text, Center, Box, Avatar, Link } from "native-base";
import { CDN } from "../../../constants/Config";
import moment from "moment-timezone";

import Util from "../../../util";

import CalendarWhiteIcon from "../../../components/svg/CalendarWhiteIcon";
import SqftIcon from "../../../components/svg/SqftIcon";
import AcreIcon from "../../../components/svg/AcreIcon";
import BedIcon from "../../../components/svg/BedIcon";
import BathIcon from "../../../components/svg/BathIcon";
import TimeIcon from "../../../components/svg/TimeIcon";
import PriceIcon from "../../../components/svg/PriceIcon";

const { vw, vh } = Util;

const HorizontalItem = ({ item, cols }) => {
  var currency = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  });
  //default pic
  let pic = "1";
  if (item && item.pic) {
    pic = item.pic;
  }
  const createdUser = item.createdUser && item.createdUser;
  // user name
  const fullname =
    createdUser && createdUser.firstname + " " + createdUser.lastname;
  //avatar fall back
  const nameSplit = fullname.split(" ");
  const fn = nameSplit[0][0];
  const ln = nameSplit[1] ? nameSplit[1][0] : "-";
  const nameFb = fn + ln;
  // avatar image source uri
  const ui =
    createdUser && createdUser.pic && createdUser.pic.id
      ? createdUser.pic.id
      : "0";
  const userUri = `${CDN}/users/${createdUser._id}/${ui}`;
  //property image uri
  const propertyUri = `${CDN}/properties/${item._id ? item._id : "-"}/${pic}`;

  let sqft = "-";
  let acre = "-";
  if (item.sqft) {
    sqft = item.sqft !== null ? item.sqft.toString() : "-";
  }

  if (item.acre) {
    acre = item.acre !== null ? item.acre.toString() : "-";
  }
  //property owner name
  const name = item.name && item.name !== "" ? item.name.trim() : "-";

  return (
    <Link
      style={{
        width: vw((window.screen.width - cols * 40) / cols),
        height: vh(210),
        marginHorizontal: vw(20),
        marginVertical: vh(20),
      }}
      href={`/properties/${item._id}`}
      // isExternal
    >
      <HStack
        style={{
          width: vw((window.screen.width - cols * 40) / cols),
          height: vh(210),
          borderRadius: vw(10),
          backgroundColor: "white",
          shadowColor: "#000",
          shadowOffset: {
            width: 0,
            height: vh(3),
          },
          shadowOpacity: 0.15,
          shadowRadius: vh(10),
          elevation: vh(5),
        }}
      >
        <Box
          style={{
            height: vh(210),
            width: vw(260),
            borderTopStartRadius: vw(10),
            borderBottomStartRadius: vw(10),
            overflow: "hidden",
            backgroundImage: `url(${propertyUri})`,
            backgroundPosition: "center",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
          }}
        />
        <VStack
          style={{
            flex: 1,
            paddingHorizontal: vw(20),
            paddingTop: vh(20),
          }}
        >
          <HStack
            style={{ justifyContent: "space-between", alignItems: "center" }}
          >
            <Text
              style={{
                fontFamily: "Poppins_medium",
                fontSize: vw(15),
                color: "rgba(48, 48, 48, 1)",
              }}
            >
              {name}
            </Text>
            <HStack alignContent={"center"}>
              <PriceIcon width={vw(24)} height={vh(24)} />
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(15),
                  color: "rgba(48, 48, 48, 1)",
                  paddingTop: vh(2),
                }}
              >
                {item.ev ? "  " + currency.format(item.ev.toString()) : "  - "}
              </Text>
            </HStack>
          </HStack>
          <HStack
            justifyContent={"space-between"}
            alignItems={"center"}
            style={{ height: vh(20), padding: 0, margin: 0 }}
          >
            <Text
              style={{
                fontFamily: "Poppins_medium",
                fontSize: vw(12),
                color: "rgba(102, 102, 102, 1)",
              }}
            >
              {item.propertyaddress}
            </Text>
            <HStack
              height={vh(20)}
              flex={1}
              justifyContent={"flex-end"}
              alignItems={"center"}
            >
              <CalendarWhiteIcon
                fill='rgba(102, 102, 102, 1)'
                width={vw(18)}
                height={vh(18)}
              />
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(12),
                  color: "rgba(102, 102, 102, 1)",
                }}
              >
                {item.yearbuilt}
              </Text>
            </HStack>
          </HStack>

          <HStack
            style={{
              justifyContent: "space-between",
              borderTopWidth: vh(2),
              borderTopColor: "rgba(0, 0, 0, 0.15)",
              borderBottomWidth: vh(2),
              borderBottomColor: "rgba(0, 0, 0, 0.15)",
              alignItems: "center",
              height: vh(39),
              marginTop: vh(20),
            }}
          >
            {/*Sqft*/}
            <HStack style={{ marginRight: vw(14), alignItems: "center" }}>
              <SqftIcon width={vw(24)} height={vh(24)} />
              <Text
                style={{
                  color: "#919191",
                  fontFamily: "Poppins_medium",
                  fontSize: vw(13),
                  marginTop: vh(1),
                }}
              >
                {sqft}
              </Text>
            </HStack>
            {/*Acre*/}
            <HStack style={{ marginHorizontal: vw(14), alignItems: "center" }}>
              <AcreIcon width={vw(24)} height={vh(24)} />
              <Text
                style={{
                  color: "#919191",
                  fontFamily: "Poppins_medium",
                  fontSize: vw(13),
                  marginTop: vh(1),
                }}
              >
                {acre}
              </Text>
            </HStack>
            {/*Bed*/}
            <HStack style={{ marginHorizontal: vw(14), alignItems: "center" }}>
              <BedIcon width={vw(24)} height={vh(24)} />
              <Text
                style={{
                  color: "#919191",
                  fontFamily: "Poppins_medium",
                  fontSize: vw(13),
                  marginTop: vh(1),
                }}
              >
                {item.bed}
              </Text>
            </HStack>
            {/*Bath*/}
            <HStack style={{ marginHorizontal: vw(7), alignItems: "center" }}>
              <BathIcon width={vw(24)} height={vh(24)} />
              <Text
                style={{
                  color: "#919191",
                  fontFamily: "Poppins_medium",
                  fontSize: vw(13),
                  marginTop: vh(1),
                }}
              >
                {item.bath}
              </Text>
            </HStack>
          </HStack>

          {/* Status and #id */}
          <HStack
            style={{
              marginTop: vh(18),
              justifyContent: "space-between",
            }}
          >
            <Text
              style={{
                fontSize: vw(13),
                fontFamily: "Poppins_medium",
                color: "rgba(125, 43, 233, 1)",
              }}
            >
              {item.status && item.status.name}
            </Text>
            <Text
              style={{
                fontSize: vw(13),
                fontFamily: "Poppins_medium",
                color: "rgba(125, 43, 233, 1)",
              }}
            >
              {"#" + item.tid}
            </Text>
          </HStack>
          {/* User Block */}
          <HStack
            style={{
              marginTop: vh(6),
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <HStack alignItems={"center"}>
              <Center
                rounded={"full"}
                style={{
                  backgroundColor: "white",
                  paddingVertical: vw(2),
                }}
              >
                <Avatar
                  source={{
                    uri: userUri,
                  }}
                  style={{
                    width: vw(24),
                    height: vh(24),
                  }}
                >
                  {nameFb}
                </Avatar>
              </Center>
              <Text
                style={{
                  paddingTop: vh(2),
                  lineHeight: vh(24),
                  fontSize: vw(13),
                  color: "rgba(104, 104, 104, 1)",
                  fontFamily: "Poppins_medium",
                  marginLeft: vw(10),
                }}
              >
                {fullname}
              </Text>
            </HStack>
            <HStack style={{ marginTop: vh(5) }} alignItems={"center"}>
              <TimeIcon width={vw(24)} height={vh(24)} />
              <Text
                style={{
                  fontSize: vw(13),
                  color: "rgba(104, 104, 104, 1)",
                  fontFamily: "Poppins_medium",
                  lineHeight: vh(24),
                }}
              >
                {moment(item.created).format("MM/DD/YYYY - hh:mm A")}
              </Text>
            </HStack>
          </HStack>
        </VStack>
      </HStack>
    </Link>
  );
};

export default HorizontalItem;
